.Image .Screen {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}
.Image .Content {
  position: absolute;
  top: 6%;
  left: 22%;
  width: 56%;
  z-index: 2;
}

.Image .Metapicture {
  position: absolute;
  top: 6%;
  left: 21.5%;
  width: 57%;
  height: 59.1%;
  z-index: 3;
  opacity: 0%;
  animation: test 8s ease-in-out infinite;
}
@keyframes test{
    0%{opacity: 0%;}
    40%{opacity: 0%;}
    50%{opacity: 100%;}
    90%{opacity: 100%;}
    100%{opacity: 0%;}
}



